@import '~plyr/dist/plyr.css';
:root {
  --bg-color: #fff;
  --text-color: #001559;
  --primary-color: #001559;
  --gray: #f1f1f1;
  --accent: #0175ff;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

@font-face {
  font-family: "Soleil Lt";
  src: url("../fonts/Soleil/Soleil-Light.woff2") format("woff2"), url("../fonts/Soleil/Soleil-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Soleil Bk";
  src: url("../fonts/Soleil/Soleil-Book.woff2") format("woff2"), url("../fonts/Soleil/Soleil-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Soleil";
  src: url("../fonts/Soleil/Soleil-Regular.woff2") format("woff2"), url("../fonts/Soleil/Soleil-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Soleil";
