  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
body {
  background: var(--bg-color);
  color: var(--text-color);
  overflow-x: hidden;
}
body.-menuOpen {
  overflow: hidden;
}
body .site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}

svg {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

.nobreak {
  white-space: nowrap;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
